.app-wrapper {
  display: grid;
  grid-template-areas: 
  "h h"
  "n c";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /* grid-gap: 10px; */
 
  margin: 0 auto;
  width: 1200px; 
}

.app-wrapper-content {
  grid-area: c;
  overflow: hidden;
  background-color: cornflowerblue;
}





